/*背景的演出效果*/
@keyframes bg_focusLeft {
    0% {
        transform: scale(1, 1) translate(0, 0);
        filter: blur(0);
    }
    100% {
        transform: scale(1.15, 1.15) translate(5%, 0);
        filter: blur(1px);
    }
}

